.overlay{
    position:absolute;
    left:0;
    right:0;
    width:100%;
    height:100%;
    background-color:rgba(50, 49, 49, 0.347);
  }

.cart{
    display:grid;
    grid-template-rows: 0.5fr 6fr 1fr;
    position:absolute;
    width:700px;
    right:0;
    height:100%;
    background-color: white;
    box-shadow:-10px 0px 20px #9d9c9c80;
    padding: 0px 50px;
  }

  .cart_list{
    overflow:auto;
  }

.title_block{
    display:grid;
    grid-template-columns: 1fr 1fr;
    }

.title_block h2{
    font-size:32px;
  }

.close_btn{
    width:fit-content;
    justify-self:end;
    align-self:center;
    padding: 10px 12px;
    background-color: white;
    color:#9D9D9D;
    border: 1px solid gray;
    border-radius: 12px;
    cursor:pointer;
  }

.total_price{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1.3fr 2fr;
  align-items:end; 
}

.total_price button{
  grid-row: 2/3;
  grid-column:1/3;
  align-self:content;
  height:fit-content;
  background-color: #c72121;
  border:0;
  border-radius:10px;
  color:white;
  font-size:22px;
  padding:20px;
  cursor:pointer;
  margin:20px;
}

.total_price button:active{
  background-color: white;
  color:#c72121;
  border: 2px solid #c72121;
}

.total_price p{
  font-size:20px;
  font-weight:bold;
}

.total_price_sum{
  justify-self:end;
}